<template>
  <div
    v-if="itemDatas && itemDatas.items && itemDatas.items.length"
    class="carousel-card carousel-custom"
    :da-expose-code="code"
  >
    <s-title v-if="['ITEM_IMAGE_CAROUSEL_2.5', 'ITEM_IMAGE_CAROUSEL_3.5', 'ITEM_IMAGE_CAROUSEL_4.5'].includes(carouselStyle.type)" :style-item="carouselStyle" :lazy-grey="LAZY_GREY" />
    <div v-else class="carousel-card__tit">{{ itemDatas.style.title }}</div>
    <div class="carousel-card__inner" ref="carousel-card">
      <ul
        class="carousel-card__scrollbox j-da-event-box"
        ref="carousel-card__scrollbox"
      >
        <li
          v-for="(cardItem, cardIndex) in itemDatas.items"
          ref="carousel-card__item"
          :key="cardIndex"
          class="carousel-card__item j-analysis-link"
          :class="{
            'carousel-custom__two-five': carouselStyle.type === 'ITEM_IMAGE_CAROUSEL_2.5',
            'carousel-custom__three-five': carouselStyle.type === 'ITEM_IMAGE_CAROUSEL_3.5',
            'carousel-custom__four-five': carouselStyle.type === 'ITEM_IMAGE_CAROUSEL_4.5'
          }"
          :data-key="cardIndex"
          da-event-expose="2-10-2"
        >
          <a
            class="carousel-card__item-link"
            :data-href="cccPublic.getCccLink && cccPublic.getCccLink({
              item: cardItem,
              cccCateInfo: cateInfo
            })"
            @click.prevent.stop="cccPublic.clickLink({
              beforeLinkJump: getBeforeLinkJumpFn(cardIndex),
              item: cardItem,
              cccCateInfo: cateInfo,
              gaIndex: cardIndex,
              scIndex: cardIndex,
              shouldUserPath: true
            })"
            tabindex="0"
            :aria-label="cardItem.ada"
            @keydown.enter.prevent="cccPublic.clickLink({
              beforeLinkJump: getBeforeLinkJumpFn(cardIndex),
              item: cardItem,
              cccCateInfo: cateInfo,
              gaIndex: cardIndex,
              scIndex: cardIndex,
              shouldUserPath: true
            })"
          >
            <div
              class="carousel-card__item-cover"
              :style="{ 'height': getImgPlaceholderHeight }"
            >
              <img
                class="lazy"
                v-lazy="{
                  src: suffix(transformImg(cardItem.imgSrc), cardItem.width),
                  loading: getLazyImg(cardItem.width, cardItem.height),
                  error: getLazyImg(cardItem.width, cardItem.height),
                }"
                lazy="loading"
                :aria-label="cardItem.ada"
              />
            </div>
            <div class="carousel-card__item-info" v-if="cardItem.title">
              <div class="carousel-card__item-tit">{{ cardItem.title }}</div>
              <div class="carousel-card__item-desc">{{ cardItem.description }}</div>
            </div>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import STitle from "./STitle.vue";
import common from './mixins/common'
import { IndexDaExpose } from './utils/index'

export default {
  name: 'CarouselCard',
  mixins: [common],
  components: {
    STitle
  },
  props: {
    propData: {
      type: Object
    },
    index: {
      type: Number
    },
    abTestData: {
      type: Object
    },
    cateInfo: {
      type: Object
    },
    tabSceneId: {
      type: [String, Number]
    }
  },
  data () {
    return {
      code: `carouselCard_${this.tabSceneId}_${this.index}`,
      itemDatas: this.propData.content.props,
      aod_id: this.propData.content.props.style.aod_id || '0',
      saName: '',
      gaName: '',
    }
  },
  computed: {
    getImgPlaceholderHeight () {
      if (this.itemDatas && this.itemDatas.items && this.itemDatas.items.length && ['ITEM_IMAGE_CAROUSEL_2.5', 'ITEM_IMAGE_CAROUSEL_3.5', 'ITEM_IMAGE_CAROUSEL_4.5'].includes(this.carouselStyle.type)) {
        const widthList = {
          'ITEM_IMAGE_CAROUSEL_2.5': 296,
          'ITEM_IMAGE_CAROUSEL_3.5': 192,
          'ITEM_IMAGE_CAROUSEL_4.5': 140,
        }
        const firstItem = this.itemDatas.items[0]
        const width = widthList[this.carouselStyle.type]
        return `${(firstItem.height * width / firstItem.width / 75).toFixed(2)}rem`
      }
      return ''
    },
    /**
     * 样式信息
     */
    carouselStyle() {
      return this.propData.content.props.style
    }
  },
  mounted () {
    this.initCccAnalysis()
    // 1. 初始化自动埋点参数
    this.initAutoAnalysis(this.$refs['carousel-card__scrollbox'])
    // 2. 绑定曝光
    IndexDaExpose.subscribe({
      keycode: `${this.code}\`2-10-2`,
      type: 'list',
      horizontal: true, // swiper
    })
  },
  deactivated() {
    IndexDaExpose.reset(this.code)
  }
}
</script>
