    font-weight: bold;
  }
  &__inner {
    position: relative;
    width: 10rem;
    padding: 0.32rem 0;
    overflow: hidden;
  }
  &__scrollbox {
    position: relative;
    padding: 0 0.32rem;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar {
      width: 0;
      height:0;
      display: none;
    }
  }
  &__item {
    position: relative;
    display: inline-block;
    width: 6.9867rem;
    height: 5.4133rem;
    vertical-align: top;
    background-color: #fff;
    &:not(:last-child) {
      margin-right: 0.213rem;
    }
  }
  &__item-link {
    height: 100%;
    display: block;
    text-decoration: none;
    position: relative;
  }
  &__item-cover {
    display: block;
    width: 6.9867rem;
    // height: 4rem;
    overflow: hidden;
    .lazy {
      display: block;
      width: 100%;
      height: auto;
      object-fit: cover;
      object-position: center center;
    }
  }
  &__item-info {
    width: 100%;
    position: absolute;
    left: 0 /*rtl:ignore*/;
    bottom: 0;
    background: #FFFFFF;
    opacity: 0.8;
    padding: 0.2133rem;
    height: 1.3733rem;
  }
  &__item-tit {
    color: #333;
    font-size: 0.3733rem;
