    text-overflow: ellipsis;
    overflow: hidden;
  }
  &__item-desc {
    color: @color_red_dark;
    font-size: 0.32rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
}
// E 轮播图 - 卡片式

.carousel-custom {
  height: auto;
  background: #fff;
