<template>
  <div class="common-title">
    <h3 tabindex="0" :aria-label="styleItem.mainTitleText" v-if="styleItem.mainTitleType === 'text' && styleItem.mainTitleText">{{ styleItem.mainTitleText }}</h3>
    <img
      v-else-if="styleItem.mainTitleType === 'image' && styleItem.mainTitleImgSrc" 
      tabindex="0" 
      class="lazy"
      v-lazy="{
        src: transformImg(styleItem.mainTitleImgSrc),
        loading: lazyGrey,
        error: lazyGrey,
      }"
      :style="{ 'height': getImgPlaceholderHeight }"
      alt=""
      lazy="loading" />
  </div>
</template>

<script>
  export default {
    props: {
      styleItem: {
        type: Object,
        default: () => {}
      },
      lazyGrey: {
        type: String,
        default: () => ''
      }
    },
    computed: {
      getImgPlaceholderHeight () {
        if (this.styleItem.width) {
          return `${(this.styleItem.height * 750 / this.styleItem.width / 75).toFixed(2)}rem`
        }
        return ''
      },
    },
    methods: {
      /**
       * 处理图片地址
       * @param {String} img
       * @param {Boolean} no_akamai
       * @description 确保你的组件有 this.context.PUBLIC_IMG
       */
      transformImg (img) {
        if (!img) return 'data:image/gif;base64,R0lGODlhAQABAAAAACwAAAAAAQABAAA='
        if(typeof gbTransfromImgByAbt === 'function') return gbTransfromImgByAbt(img)
        return img
      }
    }
  }
</script>
<style lang="less">
/* stylelint-disable selector-class-pattern, selector-max-specificity, selector-max-type  */
.j-config-index {
  .common-title {
    h3 {
      padding: 0.32rem 0.32rem 0;
      font-size: 0.3733rem;
      font-weight: bold;
    }
    img {
      width: 100%;
      cursor: pointer;
    }
  }
}

</style>
